<!-- 柱状图 -->
<style lang="stylus" scoped>
  .meter
    height 800px
    //background url('../../assets/bg.jpg') no-repeat
    background-size 100% 100%
    color white
    .meter1,.meter2,.meter3
      width 100%
      height calc(100% - 100px)
      margin-top -15px
</style>

<template>
  <div class="meter">
    <div class="meter1" style="transform: translate(-33%,0) scale(0.5)"></div>
    <div class="meter2" style="transform: translate(0%,-97%) scale(0.5)"></div>
    <div class="meter3" style="transform: translate(33%,-195%) scale(0.5)"></div>
  </div>

</template>

<script>
  import echarts from 'echarts'
  import header from '@/views/devices/header'
  import filter from '@/views/devices/filter'

  export default {
    data() {
      return {
        legendArr: [],
        myChart1: {},
        myChart2: {},
        myChart3: {},
        name: '柱状图',
        option:{
          tooltip: {
            formatter: "{a} <br/>{b} : {c}%"
          },
          title: {
            text: '平均网络带宽',
            x:'center',
            textStyle: {//标题内容的样式
              color: '#000000',//
              fontSize:50
            }
          },
          series: [{
            name: "仪盘表",
            type: "gauge",
            splitNumber:60,
            axisLine: {
              "lineStyle": {
                "color": [
                  [0.31, "#333"],
                  [1, "#333"]
                ],
                "width": 20
              },

            },
            axisTick: {
              lineStyle: {
                color:'#ccc',
                width: 2
              },
              length: 20,
              splitNumber:2
            },
            pointer: {           // 分隔线
              shadowColor : '#fff', //默认透明
              shadowBlur: 5,
              show:false
            },
            axisLabel: {
              distance: 10,
              textStyle: {
                color: "#fff"
              },
              show:false,
            },
            splitLine: {
              "show": false
            },
            itemStyle: {
              normal: {
                color: "#494f50"
              }
            },
            detail: {
              formatter: "{value}bit/s",
              offsetCenter: [0, "30%"],
              textStyle: {
                fontSize: 70,
                color: "#F37B1D"
              }
            },
            title: {
              offsetCenter: [0, "60%"]
            },
            data: [{
              name: "",
              value: 31
            }]
          }]
        },
        option2:{
          title: {
            text: 'CPU',
            x:'center',
            textStyle: {//标题内容的样式
              color: '#000000',//
              fontSize:50
            }
          },
          tooltip: {
            formatter: "{a} <br/>{b} : {c}%"
          },
          series: [{

            name: "仪盘表",
            type: "gauge",
            splitNumber:60,
            axisLine: {
              "lineStyle": {
                "color": [
                  [0.31, "#333"],
                  [1, "#333"]
                ],
                "width": 20
              },

            },
            axisTick: {
              lineStyle: {
                color:'#ccc',
                width: 2
              },
              length: 20,
              splitNumber:2
            },
            pointer: {           // 分隔线
              shadowColor : '#fff', //默认透明
              shadowBlur: 5,
              show:false
            },
            axisLabel: {
              distance: 10,
              textStyle: {
                color: "#fff"
              },
              show:false,
            },
            splitLine: {
              "show": false
            },
            itemStyle: {
              normal: {
                color: "#494f50"
              }
            },
            detail: {
              formatter: "{value}%",
              offsetCenter: [0, "30%"],
              textStyle: {
                fontSize: 70,
                color: "#F37B1D"
              }
            },
            title: {
              offsetCenter: [0, "60%"]
            },
            data: [{
              name: "",
              value: 20
            }]
          }]
        },
        option3:{
          title: {
            text: '内存',
            x:'center',
            textStyle: {//标题内容的样式
              color: '#000000',//
              fontSize:50
            }
          },
          tooltip: {
            formatter: "{a} <br/>{b} : {c}%"
          },
          series: [{
            name: "仪盘表",
            type: "gauge",
            splitNumber:60,
            axisLine: {
              "lineStyle": {
                "color": [
                  [0.31, "#333"],
                  [1, "#333"]
                ],
                "width": 20
              },

            },
            axisTick: {
              lineStyle: {
                color:'#ccc',
                width: 2
              },
              length: 20,
              splitNumber:2
            },
            pointer: {           // 分隔线
              shadowColor : '#fff', //默认透明
              shadowBlur: 5,
              show:false
            },
            axisLabel: {
              distance: 10,
              textStyle: {
                color: "#fff"
              },
              show:false,
            },
            splitLine: {
              "show": false
            },
            itemStyle: {
              normal: {
                color: "#494f50"
              }
            },
            detail: {
              formatter: "{value}%",
              offsetCenter: [0, "30%"],
              textStyle: {
                fontSize: 70,
                color: "#F37B1D"
              }
            },
            title: {
              offsetCenter: [0, "60%"]
            },
            data: [{
              name: "",
              value: 50
            }]
          }]
        },
      }
    },
    methods: {
      _init1() {
        this.legendArr = this.myChart1.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart1)
        window.addEventListener('resize', function() {
          this.myChart1.resize()
        }.bind(this))
      },
      _init2() {
        this.legendArr = this.myChart2.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart2)
        window.addEventListener('resize', function() {
          this.myChart2.resize()
        }.bind(this))
      },
      _init3() {
        this.legendArr = this.myChart3.getOption().series
        this.legendArr.forEach((data) => {
          data.selected = true;
        })
        this.$root.charts.push(this.myChart3)
        window.addEventListener('resize', function() {
          this.myChart3.resize()
        }.bind(this))
      },
      detectionData(str) {
        var color = new echarts.graphic.LinearGradient(0, 0, 1, 1, [{
          offset: 0,
          color: 'rgba(243, 238,67, 1)'
        }, {
          offset: 0.5,
          color: 'rgba(225, 128,78, 1)'
        }, {
          offset:1,
          color: 'rgba(237, 92,79, 1)'
        }]);
        if (str >= 30 && str <= 60) {
          color =  new echarts.graphic.LinearGradient(0, 0, 1, 1, [{
            offset: 0,
            color: 'rgba(243, 238,67, 1)'
          }, {
            offset: 0.5,
            color: 'rgba(225, 128,78, 1)'
          }, {
            offset:1,
            color: 'rgba(237, 92,79, 1)'
          }]);
        } else if (str > 60) {
          color = new echarts.graphic.LinearGradient(0, 0, 1, 1, [{
            offset: 0,
            color: 'rgba(243, 238,67, 1)'
          }, {
            offset: 0.5,
            color: 'rgba(225, 128,78, 1)'
          }, {
            offset:1,
            color: 'rgba(237, 92,79, 1)'
          }]);
        }
        return color;
      },
      refreshData1(){
        setInterval(()=>{
          var value = (Math.random() * 100).toFixed(2) - 0;
          this.option.series[0].data[0].value = value;
          this.option.series[0].axisLine.lineStyle.color[0][0] = value / 100;
          this.option.series[0].axisLine.lineStyle.color[0][1] = this.detectionData(value);
          this.myChart1.setOption(this.option, true);
        }, 1500);
      },
      refreshData2(){
        setInterval(()=>{
          var value = (Math.random() * 50).toFixed(2) - 0;
          this.option2.series[0].data[0].value = value;
          this.option2.series[0].axisLine.lineStyle.color[0][0] = value / 100;
          this.option2.series[0].axisLine.lineStyle.color[0][1] = this.detectionData(value);
          this.myChart2.setOption(this.option2, true);
        }, 5500);
      },
      refreshData3(){
        setInterval(()=>{
          var value = (Math.random() * 100).toFixed(2) - 0;
          this.option3.series[0].data[0].value = value;
          this.option3.series[0].axisLine.lineStyle.color[0][0] = value / 100;
          this.option3.series[0].axisLine.lineStyle.color[0][1] = this.detectionData(value);
          this.myChart3.setOption(this.option3, true);
        }, 4500);
      }
    },
    components: {
      'v-header': header,
      'v-filter': filter
    },
    mounted() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart1 = echarts.init(document.querySelector('.meter .meter1'));
      this.myChart2 = echarts.init(document.querySelector('.meter .meter2'));
      this.myChart3 = echarts.init(document.querySelector('.meter .meter3'));
      this.myChart1.setOption(this.option);
      this._init1();
      this.refreshData1();
      this.myChart2.setOption(this.option2);
      this._init2();
      this.refreshData2();
      this.myChart3.setOption(this.option3);
      this._init3();
      this.refreshData3();


    }
  }

</script>



