<template>
  <div>
    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
      <el-col :span="24" offset="10"><h1 style="padding-left: 32px;padding-bottom: 20px;font-size: 30px" >添加设备</h1></el-col>
      <el-col :span="24" offset="6" class="toolbar" style="padding-bottom: 0;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-input v-model="filters.number" placeholder="请输入设备编号(代码)" auto-complete="off" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-input  placeholder="请输入设备名称" auto-complete="off" @keyup.enter.native="handleSearch"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="medium" v-on:click="handleSearch">查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="medium" @click="showAddDialog">新增</el-button>
          </el-form-item>
        </el-form>
      </el-col>
      <!--表格数据-->
      <el-table :data="tableData" :row-class-name="tableRowClassName" border style="width: 100%">
        <el-table-column prop="mId" v-if="idShow" label="ID"></el-table-column>
        <el-table-column prop="mNumber" label="设备编号" width="150" sortable align="center"></el-table-column>
        <el-table-column prop="name" label="设备名称" width="200" align="center"></el-table-column>
        <el-table-column prop="mType" label="设备类型" width="120" align="center"></el-table-column>
        <el-table-column prop="mContent" label="设备状态" show-overflow-tooltip width="100" align="center"></el-table-column>
        <el-table-column prop="createTime" label="创建日期" width="150" align="center"></el-table-column>
        <el-table-column prop="depart" label="所属车间" width="100" align="center"></el-table-column>
        <el-table-column prop="factory" label="所属水厂" width="150" align="center"></el-table-column>
        <el-table-column prop="eRemark" label="备注" width="180" align="center"></el-table-column>
        <el-table-column label="操作" align="center" width="180">
          <template slot-scope="scope">
            <el-button
              size="mini" @click="handleDetail(scope.$index, scope.row)">详情</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24" class="toolbar">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange"
                       :current-page="currentPage" :page-sizes="[10, 50, 100, 200]" :page-size="10"
                       layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </el-col>
    </el-col>

    <el-dialog title="设备添加" :close-on-press-escape="false" :close-on-click-modal="false" :visible.sync="dialogAddVisible" :before-close="handleClose">
      <el-form :model="form">
        <el-form-item label="设备名称：" :label-width="formLabelWidth">
          <el-input v-model="form.name" placeholder="请输入设备名称" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="所属车间：" :label-width="formLabelWidth">
          <el-select v-model="form.depart" placeholder="请选择所属车间">
            <el-option label="车间1" value="receive"></el-option>
            <el-option label="车间2" value="forward"></el-option>
            <el-option label="车间3" value="receive"></el-option>
            <el-option label="车间4" value="forward"></el-option>
            <el-option label="车间5" value="receive"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="设备类型：" :label-width="formLabelWidth">
          <el-select v-model="form.type" placeholder="请选择设备类型">
            <el-option label="PLC" value="receive"></el-option>
            <el-option label="IPC" value="forward"></el-option>
            <el-option label="RTU" value="receive"></el-option>
            <el-option label="DSC" value="forward"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="IP地址：" :label-width="formLabelWidth">
          <el-input v-model="form.ip" placeholder="请输入IP地址" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="MAC地址：" :label-width="formLabelWidth">
          <el-input v-model="form.mac" placeholder="请输入MAC地址" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="设备备注：" :label-width="formLabelWidth">
          <el-input v-model="form.remark" type="textarea" placeholder="请填写设备备注"></el-input>
        </el-form-item>
        <el-form-item label="创建日期：" :label-width="formLabelWidth">
          <el-date-picker v-model="form.createTime" type="date" placeholder="请选择日期"></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="dialogAddVisible = false">取消</el-button>
        <el-button type="primary" @click="openMsg">提交</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
  import Header from "./header";
  export default {
    name: 'deviceopration',
    components: {Header},
    data: function(){
      return {
        loading: false,
        radio: '1',
        idShow: false,
        keyword: "集团",
        total:6,
        currentPage: 1,
        pageSize: 10,
        tableData: [{
          mId: 1,
          mNumber: 'PLC_001_001',
          name: 's7_200 PLC OCSBV001',
          mType: 'PLC',
          mContent: '在线',
          eRemark: '正常',
          createTime: '2016-11-17',
          depart: '车间001',
          factory: '四川水厂'
        },
          {
            mId: 2,
            mNumber: 'PLC_001_002',
            name: 's7_200 PLC OCSBV002',
            mType: 'PLC',
            mContent: '离线',
            eRemark: '正常',
            createTime: '2016-11-17',
            depart: '车间001',
            factory: '四川水厂'
          },{
            mId: 3,
            mNumber: 'PLC_001_003',
            name: 's7_400 PLC MMI002',
            mType: 'PLC',
            mContent: '离线',
            eRemark: '正常',
            createTime: '2016-11-17',
            depart: '车间001',
            factory: '四川水厂'
          },{
            mId: 4,
            mNumber: 'PLC_001_004',
            name: 'Modbus Alex002',
            mType: 'PLC',
            mContent: '离线',
            eRemark: '正常',
            createTime: '2016-11-17',
            depart: '车间001',
            factory: '四川水厂'
          },{
            mId: 5,
            mNumber: 'PLC_001_005',
            name: 'Modbus Alex003',
            mType: 'PLC',
            mContent: '离线',
            eRemark: '正常',
            createTime: '2016-11-19',
            depart: '车间001',
            factory: '四川水厂'
          },{
            mId: 6,
            mNumber: 'PLC_001_006',
            name: 'Bacnet Css581_01',
            mType: 'PLC',
            mContent: '在线',
            eRemark: '正常',
            createTime: '2016-11-18',
            depart: '车间001',
            factory: '四川水厂'
          }],
        form: {
          name: '',
          type: '',
          depart: '',
          remark: '',
          createTime: '',
          ip: '',
          mac: '',
        },
        formLabelWidth: '120px',
        dialogAddVisible: false,
        filters: {
          number: ''
        }
      };
    },
    methods: {
      handleSearch(){
        console.info(this.filters.number);
      },
      handleDetail(index, row) {
        console.log(index, row);
        console.log(row.mId);
        this.$router.push('/devicesinfo'); //用go刷新
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex === 1 || rowIndex === 2 || rowIndex === 3 || rowIndex === 4) {
          return 'medium-row';
        }
        return '';
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleSizeChange(val){
//        this.pageSize = val;
//        this.currentPage = 1;
      },
      handleCurrentChange(val){
//        this.currentPage = val;
      },
      showAddDialog(){
        this.dialogAddVisible = true;
      },
      handleClose(done){  //关闭弹窗
        done();
      },
      openMsg() {
        this.$message({
          showClose: true,
          message: '添加成功',
          type: 'success'
        })
        this.dialogAddVisible = false;

      }
    }
  }
</script>
<style>
  .el-table .medium-row {
    background: #fff6f6;
  }
  .el-table .low-row {
    background: #fffdf4;
  }
  .el-table .high-row {
    background: #ffdad8;
  }
</style>



