    <strong style="font-family: 黑体;font-size: x-large;text-align:left">告警记录</strong>
    <div class="select">
      <el-table ref="Table" :row-class-name="tableRowClassName" :data="tableData" tooltip-effect="dark" style="width:100%" border="true">
        <el-table-column label="告警编号" prop="id" align="center" width="80px"></el-table-column>
        <el-table-column prop="name" label="设备名称" align="center" width="180px"></el-table-column>
        <el-table-column prop="type" label="告警类别" align="center" width="100px"></el-table-column>
        <el-table-column prop="people" label="告警内容" align="center"></el-table-column>
        <el-table-column prop="username" label="是否解决" align="center" width="80px"></el-table-column>
        <el-table-column prop="dictionary" label="告警时间"  align="center" width="180px"></el-table-column>
