  const cityOptions = ['超级管理员', '管理员', '审计员', '普通用户'];
  export default {
    data() {
      return {
        total:10,
        checkedCities: [],
        formLabelWidth: '120px',
        dialogAddVisible:false,
        dialogdescripption:false,
        cities: cityOptions,
        isIndeterminate: true,
        show3:false,
        selectItem:{
          user:'',
          factory:[],
          position:'',
          depart:''
        },
        tableData: [{
