        });
      },
      onSubmit() {
        console.log('submit!');
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      showAddDialog(){
        console.log(this.dialogAddVisible);
        this.dialogAddVisible = true;
      },
      showDetail(){
        console.log(this.dialogdescripption);
        this.dialogdescripption = true;

      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
      },
      tableRowClassName({row, rowIndex}) {
        if (rowIndex === 0 || rowIndex === 3 || rowIndex === 4 || rowIndex === 5 || rowIndex === 6 || rowIndex === 7|| rowIndex === 8) {
          return 'high-row';
        } else {
          return 'low-row';
        }
        return '';
      },
      handleClose(done){  //关闭弹窗
        done();
      }
    }
  }
</script>
<style>
  .select{
    position: relative;
    width: 100%;
    margin: 0 auto;
    background:#fff;
    padding: 20px;
    border-radius: 10px;
    overflow: hidden;
  }
  .btndiv{
    width: 100%;
    margin:20px 0px 10px 0px;
    text-align: left;
  }
  .mybutton{
    display: inline-block;
    margin-left:5px;
    margin-right: 5px;
  }

  .el-table .medium-row {
    background: #fff6f6;
  }
  .el-table .low-row {
    background: #fffdf4;
  }
  .el-table .high-row {
    background: #ffdad8;
  }
</style>




